.header {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url(../img/header-bg.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &__top {
    .wrapper();
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 40px;
    color: @color-white;
  }

  &__logo {
    font-family: "Lato";
    font-size: 25px;
    font-weight: 900;
    letter-spacing: 0.4px;

    span {
      margin-right: 10px;
      color: @color-red;
    }
  }

  .menu {
    &__list {
      display: flex;
      justify-content: space-between;
      min-width: 372px;
    }

    &__link {
      font-family: "Lato";
      font-size: 16px;
      font-weight: 700;
      letter-spacing: 0.4px;
      transition: .3s;

      &:hover {
        color: @color-red;
      }
    }
  }

  &__content {
    .wrapper();
    margin: auto;
    margin-top: 12vw;
    text-align: center;

    h1 {
      font-family: "Lato";
      font-size: 46px;
      font-weight: 300;
      text-transform: uppercase;
      letter-spacing: 1.15px;
      color: @color-white;

      span {
        font-weight: 900;
      }
    }

    p {
      font-family: "Lato";
      font-size: 22px;
      font-weight: 300;
      line-height: 38px;
      letter-spacing: 0.54px;
      color: @color-white;
    }

    .btn {
      margin-top: 65px;
      padding: 15px 45px;
      transition: 0.4s;

      &:hover {
        color: @color-red;
        background-color: @color-white;
      }
    }
  }
}
