.slider {
  width: 100%;
  height: 500px;
  background-image: url(../img/slider-bg.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  .swiper-container {
    .wrapper();
    height: 100%;
    width: 100%;

    .swiper-slide {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;


      .mark-top {
        margin-bottom: 30px;
      }

      .mark-bottom {
        margin-top: 30px;
      }

      p {
        max-width: 824px;
        font-family: "Lato";
        font-style: italic;
        font-size: 25px;
        font-weight: 300;
        line-height: 40px;
        letter-spacing: 0.62px;
        color: rgba(255, 255, 255, .8);
        text-align: center;
      }
    }

    .swiper-button-prev,
    .swiper-button-next {
      color: rgba(255, 255, 255, .8);
    }
  }
}
